import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

let defaultCity = '深圳';
let defaultType = '新品首发';
let defaultUserid = '';

defaultCity = localStorage.city
defaultType = localStorage.type
defaultUserid = localStorage.userid

const state = {
  city: defaultCity,
  type: defaultType,
  userid: defaultUserid
}

const mutations = {
  changeCity(state, cityName) {
    state.city = cityName;
    localStorage.city = cityName
  },
  changeType(state, TypeName) {
    state.type = TypeName;
    localStorage.type = TypeName
  },
  changeUserid(state, Userid) {
    state.userid = Userid;
    localStorage.userid = Userid
  },
  changeUidtonull() {
    localStorage.userid = ''
  }
}

export default new Vuex.Store({
  state,
  mutations
})



