<template>
  <div class="goods">
    <el-row>
      <el-col :span="12" style="margin-bottom: 10px">
        <el-button type="primary" @click="add">新增</el-button>
        <el-input
          v-model="name"
          style="width: 50%"
          placeholder="请输入内容回车搜索"
          @keyup.enter.native="loadTable(1)"
        ></el-input>
      </el-col>
    </el-row>
    <el-table :data="page.content">
      <el-table-column prop="goodsid" label="ID" width="140"> </el-table-column>
      <el-table-column prop="date" label="日期" width="140"> </el-table-column>
      <el-table-column prop="name" label="名称" width="120"> </el-table-column>
      <el-table-column prop="detailedpicture" label="名称" width="120">
      </el-table-column>
      <el-table-column label="照片" width="120">
        <template slot-scope="scope">
          <el-popover placement="top-start" title="" trigger="hover">
            <img
              :src="
                'https://m31402.jp.cdjxt.net/suixinpei/totalimg/' +
                scope.row.detailedpicture +
                '.png'
              "
              alt=""
              style="width: 150px; height: 150px"
            />
            <img
              slot="reference"
              :src="
                'https://m31402.jp.cdjxt.net/suixinpei/totalimg/' +
                scope.row.detailedpicture +
                '.png'
              "
              style="width: 30px; height: 30px"
            />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="price" label="价格"> </el-table-column>
      <el-table-column
        prop="inventory"
        label="库存"
        width="120"
      ></el-table-column>
      <el-table-column prop="introduction" label="风格" width="120">
      </el-table-column>
      <el-table-column prop="type" label="男/女装" width="120">
      </el-table-column>
      <el-table-column prop="clotheskind" label="类型"> </el-table-column>
      <el-table-column fixed="right" label="操作" width="100">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="small"
            circle
            @click="edit(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="small"
            circle
            @click="del(scope.row.goodsid)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row type="flex" justify="center" style="margin-top: 10px">
      <el-pagination
        layout="prev, pager, next"
        :page-size="pageSize"
        :current-page="pageNum"
        @prev-click="loadTable"
        @current-change="loadTable"
        @next-click="loadTable"
        :total="page.totalElements"
      >
      </el-pagination>
    </el-row>
    <el-dialog title="用户信息" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="ID" style="display: none">
          <el-input v-model="form.goodsid"></el-input>
        </el-form-item>
        <el-form-item label="日期">
          <el-input v-model="form.date"></el-input>
        </el-form-item>
        <el-form-item label="商品名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="照片名">
          <el-input disabled v-model="form.detailedpicture"></el-input>
        </el-form-item>
        <el-form-item label="照片">
          <el-upload
            action=""
            :auto-upload="false"
            :limit="5"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :on-change="fileChange"
            :multiple="false"
            :file-list="fileList"
            list-type="picture"
          >
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="价格">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="库存">
          <el-input v-model="form.inventory"></el-input>
        </el-form-item>
        <el-form-item label="风格">
          <el-input v-model="form.introduction"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio v-model="form.type" label="男装">男装</el-radio>
          <el-radio v-model="form.type" label="女装">女装</el-radio>
        </el-form-item>
        <el-form-item label="类型">
          <el-input v-model="form.clotheskind"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button
          type="primary"
          @click="
            save();
            uploadImage(form.detailedpicture);
          "
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      photoname: 1,
      page: [],
      name: "",
      pageNum: 1,
      pageSize: 8,
      dialogVisible: false,
      form: {},
      goods: [],

      dialogImageUrl: "",
      dialogVisible: false,
      fileList: [],
    };
  },
  computed: {},
  created() {
    this.loadTable(this.pageNum);
  },
  methods: {
    loadTable(num) {
      this.pageNum = num;
      this.$axios
        .get(
          "http://localhost:8888/data_goods/page?pageNum=" +
            this.pageNum +
            "&pageSize=" +
            this.pageSize +
            "&name=" +
            this.name
        )
        .then((res) => {
          this.page = res.data.data;
        });
      this.getgoods();
    },
    getgoods() {
      var that = this;
      var url = "http://localhost:8888/data_goods";
      that.$axios.get(url).then(function (result) {
        const data = result.data.data;
        that.goods = data;
        that.photoname = that.goods.length;
        // console.log(that.goods.length);
      });
    },
    add() {
      this.dialogVisible = true;
      this.form = {};
    },
    edit(row) {
      this.form = row;
      this.dialogVisible = true;
    },
    save() {
      // let data = JSON.stringify(this.form);
      if (this.form.goodsid) {
        if (this.form.detailedpicture == null) {
          this.form.detailedpicture = this.photoname;
        } else {
        }
        this.$axios
          .put("http://localhost:8888/data_goods", {
            goodsid: this.form.goodsid,
            name: this.form.name,
            detailedpicture: this.form.detailedpicture,
            price: this.form.price,
            date: this.form.date,
            inventory: this.form.inventory,
            introduction: this.form.introduction,
            type: this.form.type,
            clotheskind: this.form.clotheskind,
          })
          .then((successResponse) => {
            this.dialogVisible = false;
            this.loadTable(this.pageNum);
          })
          .catch((failResponse) => {});
      } else {
        // 新增
        this.$axios
          .post("http://localhost:8888/data_goods", {
            goodsid: this.form.goodsid,
            name: this.form.name,
            detailedpicture: this.photoname,
            price: this.form.price,
            date: this.form.date,
            inventory: this.form.inventory,
            introduction: this.form.introduction,
            type: this.form.type,
            clotheskind: this.form.clotheskind,
          })
          .then((successResponse) => {
            this.dialogVisible = false;
            this.loadTable(this.pageNum);
          })
          .catch((failResponse) => {});
      }
    },
    del(delid) {
      this.$axios
        .get("http://localhost:8888/data_goods/delete/" + delid)
        .then((successResponse) => {
          this.dialogVisible = false;
          this.loadTable(this.pageNum);
        })
        .catch((failResponse) => {});
    },
    handleRemove(file, fileList) {
      this.fileList = fileList;
    },
    fileChange(file, fileList) {
      this.fileList = fileList;
    },
    handlePreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    uploadImage(pname) {
      let formData = new FormData();
      for (let i = 0; i < this.fileList.length; i++) {
        if (pname == null) {
          this.fileList[i].name = this.photoname + ".jpg";
        } else {
          this.fileList[i].name = pname + ".jpg";
        }
        formData.append("photos", this.fileList[i].raw, this.fileList[i].name);
        console.log(this.fileList[i].name);
      }
      let config = { "Content-Type": "multipart/form-data" };
      // let this_obj = this;
      this.$axios
        .post("http://localhost:8888/update/photos", formData, config)
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {},
  activated() {},
};
</script>
<style scoped>
</style>


